import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './components/Home';
import Market from './components/Market';
import FarmerPanel from './components/FarmerPanel';
import BuyerPanel from './components/BuyerPanel';
import AdminPanel from './components/AdminPanel';
import Auth from './components/Auth';
import Profile from './components/Profile';
import About from './components/About';
import Contact from './components/Contact';
import Navbar from './components/Navbar';
import './App.css';

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const savedUser = localStorage.getItem('user');
    if (savedUser) {
      setUser(JSON.parse(savedUser));
    }
    setLoading(false);
  }, []);

  const handleLogin = (userData) => {
    setUser(userData);
  };

  const handleLogout = () => {
    setUser(null);
  };

  const handleUpdateProfile = (updatedUser) => {
    setUser(updatedUser);
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-2xl">Loading...</div>
      </div>
    );
  }

  if (!user) {
    return <Auth onLogin={handleLogin} />;
  }

  return (
    <Router>
      <div className="App">
        <Navbar user={user} />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/market" element={<Market />} />
          <Route path="/farmer" element={<FarmerPanel user={user} />} />
          <Route path="/buyer" element={<BuyerPanel />} />
          <Route path="/admin" element={<AdminPanel />} />
          <Route path="/profile" element={<Profile user={user} onLogout={handleLogout} onUpdateProfile={handleUpdateProfile} />} />
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;