import React, { useState, useEffect } from 'react';

export default function BuyerPanel() {
  const [searchTerm, setSearchTerm] = useState('');
  const [isListening, setIsListening] = useState(false);
  const [filterBy, setFilterBy] = useState('all');
  const [listings, setListings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [language, setLanguage] = useState('en');

  const spiceInfo = {
    cardamom: { label: "Cardamom", image: "/images/cardamom.png", ml: "" },
    pepper: { label: "Black Pepper", image: "/images/pepper.png", ml: "" },
    clove: { label: "Clove", image: "/images/clove.png", ml: "" },
    nutmeg: { label: "Nutmeg", image: "/images/nutmeg.png", ml: "" },
    cinnamon: { label: "Cinnamon", image: "/images/cinnamon.png", ml: "" },
    vanilla: { label: "Vanilla", image: "/images/vanilla.png", ml: "" }
  };

  useEffect(() => {
    fetchListings();
  }, []);

  const fetchListings = async () => {
    try {
      const API_URL = process.env.REACT_APP_API_URL;
      const response = await fetch(`${API_URL}/api/listings`);
      const data = await response.json();
      setListings(data);
    } catch (error) {
      console.error('Error fetching listings:', error);
    } finally {
      setLoading(false);
    }
  };

  const startVoiceSearch = () => {
    const SpeechRecognition = window.SpeechRecognition || 
                             window.webkitSpeechRecognition || 
                             window.mozSpeechRecognition || 
                             window.msSpeechRecognition;
    
    if (!SpeechRecognition) {
      alert(' Voice search not supported in this browser.');
      return;
    }

    try {
      const recognition = new SpeechRecognition();
      recognition.lang = language === 'ml' ? 'ml-IN' : 'en-IN';
      recognition.continuous = false;
      recognition.interimResults = false;
      recognition.maxAlternatives = 1;

      setIsListening(true);
      recognition.start();

      recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        setSearchTerm(transcript);
        setIsListening(false);
      };

      recognition.onerror = (event) => {
        console.error('Voice search error:', event.error);
        setIsListening(false);
        
        if (event.error === 'not-allowed') {
          alert(' Microphone access denied.');
        } else if (event.error === 'no-speech') {
          alert(' No speech detected. Try again.');
        }
      };

      recognition.onend = () => {
        setIsListening(false);
      };
    } catch (error) {
      console.error('Voice search setup error:', error);
      alert(' Voice search failed.');
      setIsListening(false);
    }
  };

  const filteredListings = listings.filter(listing => {
    const spice = spiceInfo[listing.spice];
    if (!spice) return false;
    
    const matchesSearch = spice.label.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         spice.ml.includes(searchTerm);
    
    if (filterBy === 'all') return matchesSearch;
    if (filterBy === 'low-price') return matchesSearch && listing.price < 1000;
    if (filterBy === 'high-quantity') return matchesSearch && listing.quantity > 30;
    return matchesSearch;
  });

  const handleCall = (phone) => {
    window.location.href = `tel:${phone}`;
  };

  const handleOrder = async (listing) => {
    try {
      const orderData = {
        spice: listing.spice,
        quantity: listing.quantity,
        price: listing.price,
        farmerId: listing.userId,
        farmerName: listing.farmer,
        farmerPhone: listing.phone,
        location: listing.location
      };

      const API_URL = process.env.REACT_APP_API_URL;
      const response = await fetch(`${API_URL}/create-order`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(orderData)
      });

      if (response.ok) {
        const result = await response.json();
        alert(` Order placed successfully! Order ID: ${result.orderId || 'N/A'}`);
      } else {
        throw new Error('Order creation failed');
      }
    } catch (error) {
      console.error('Order error:', error);
      alert(` Order failed. Calling farmer directly at ${listing.phone}`);
      handleCall(listing.phone);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-50 to-green-50 pb-20">
      <header className="bg-white shadow-sm p-4">
        <div className="flex justify-between items-center max-w-md mx-auto mb-2">
          <h1 className="text-xl font-bold text-blue-700">Buy Spices</h1>
          <button
            onClick={() => setLanguage(language === 'en' ? 'ml' : 'en')}
            className="px-3 py-1 bg-blue-100 rounded-full text-sm font-medium"
          >
            {language === 'en' ? '' : 'English'}
          </button>
        </div>
        <p className="text-center text-sm text-gray-600"> </p>
      </header>

      <div className="p-4 max-w-md mx-auto">
        <div className="bg-white rounded-2xl p-4 shadow-lg mb-6">
          <h2 className="text-lg font-bold mb-4 text-center">Search Spices</h2>
          
          <div className="flex gap-2 mb-4">
            <input
              type="text"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Search spices..."
              className="flex-1 p-3 border rounded-lg text-lg"
            />
            <button
              onClick={startVoiceSearch}
              disabled={isListening}
              className={`p-3 rounded-lg transition-all ${
                isListening 
                  ? 'bg-red-500 text-white animate-pulse' 
                  : 'bg-blue-500 hover:bg-blue-600 text-white'
              }`}
            >
              <span className="text-xl"></span>
            </button>
          </div>

          <div className="flex gap-2 overflow-x-auto">
            <button
              onClick={() => setFilterBy('all')}
              className={`px-4 py-2 rounded-full whitespace-nowrap ${
                filterBy === 'all' ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-700'
              }`}
            >
              All
            </button>
            <button
              onClick={() => setFilterBy('low-price')}
              className={`px-4 py-2 rounded-full whitespace-nowrap ${
                filterBy === 'low-price' ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-700'
              }`}
            >
              Low Price
            </button>
            <button
              onClick={() => setFilterBy('high-quantity')}
              className={`px-4 py-2 rounded-full whitespace-nowrap ${
                filterBy === 'high-quantity' ? 'bg-blue-500 text-white' : 'bg-gray-200 text-gray-700'
              }`}
            >
              High Quantity
            </button>
          </div>
        </div>

        <div className="space-y-4">
          {loading ? (
            <div className="text-center py-8">
              <div className="text-4xl mb-4"></div>
              <p className="text-gray-600">Loading listings...</p>
            </div>
          ) : filteredListings.length === 0 ? (
            <div className="text-center py-8">
              <div className="text-4xl mb-4"></div>
              <p className="text-gray-600">No spices found</p>
            </div>
          ) : (
            filteredListings.map(listing => {
              const spice = spiceInfo[listing.spice];
              return (
                <div key={listing.id} className="bg-white rounded-2xl p-6 shadow-lg">
                  <div className="flex items-start gap-4">
                    <img 
                      src={spice?.image} 
                      alt={spice?.label}
                      className="w-16 h-16 object-cover rounded-lg"
                      onError={(e) => { e.target.src = '/images/placeholder.svg'; }}
                    />
                    
                    <div className="flex-1">
                      <h3 className="text-xl font-bold">{spice?.label}</h3>
                      <p className="text-sm text-gray-600 mb-2">{spice?.ml}</p>
                      
                      <div className="space-y-2 mb-4">
                        <div className="flex justify-between">
                          <span className="text-gray-600">Price:</span>
                          <span className="font-bold text-green-600">{listing.price}/kg</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Available:</span>
                          <span className="font-bold">{listing.quantity} kg</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Farmer:</span>
                          <span className="font-medium">{listing.farmer}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">Location:</span>
                          <span className="font-medium">{listing.location}</span>
                        </div>
                      </div>

                      <div className="flex gap-2">
                        <button
                          onClick={() => handleCall(listing.phone)}
                          className="flex-1 bg-green-500 hover:bg-green-600 text-white p-3 rounded-xl font-bold flex items-center justify-center gap-2"
                        >
                          <span></span>
                          Call
                        </button>
                        <button
                          onClick={() => handleOrder(listing)}
                          className="flex-1 bg-blue-500 hover:bg-blue-600 text-white p-3 rounded-xl font-bold flex items-center justify-center gap-2"
                        >
                          <span></span>
                          Order
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })
          )}
        </div>
      </div>
    </div>
  );
}