import React, { useEffect, useState } from "react";
import axios from "axios";
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis } from "recharts";

export default function Market() {
  const [priceData, setPriceData] = useState([]);
  const [selectedSpice, setSelectedSpice] = useState("cardamom");
  const [currentPrice, setCurrentPrice] = useState(null);
  const [language, setLanguage] = useState('en');

  const spices = [
    { name: "pepper", label: "Black Pepper", image: "/images/pepper.png", ml: "" },
    { name: "cardamom", label: "Cardamom", image: "/images/cardamom.png", ml: "" },
    { name: "clove", label: "Clove", image: "/images/clove.png", ml: "" },
    { name: "nutmeg", label: "Nutmeg", image: "/images/nutmeg.png", ml: "" },
    { name: "cinnamon", label: "Cinnamon", image: "/images/cinnamon.png", ml: "" },
    { name: "vanilla", label: "Vanilla", image: "/images/vanilla.png", ml: "" }
  ];

  const numberToMalayalam = (num) => {
    const ones = ['', '', '', '', '', '', '', '', '', ''];
    const tens = ['', '', '', '', '', '', '', '', '', ''];
    const hundreds = ['', '', '', '', '', '', '', '', '', ''];
    
    if (num === 0) return '';
    if (num < 10) return ones[num];
    if (num === 10) return '';
    if (num < 20) return ones[num - 10] + '';
    if (num < 100) {
      const ten = Math.floor(num / 10);
      const one = num % 10;
      return tens[ten] + (one > 0 ? ones[one] : '');
    }
    if (num < 1000) {
      const hundred = Math.floor(num / 100);
      const remainder = num % 100;
      return hundreds[hundred] + (remainder > 0 ? ' ' + numberToMalayalam(remainder) : '');
    }
    if (num < 100000) {
      const thousand = Math.floor(num / 1000);
      const remainder = num % 1000;
      return numberToMalayalam(thousand) + ' ' + (remainder > 0 ? ' ' + numberToMalayalam(remainder) : '');
    }
    return num.toString(); // Fallback for very large numbers
  };

  const speak = (text) => {
    if (!('speechSynthesis' in window)) {
      console.warn('Text-to-speech not supported in this browser');
      return;
    }

    try {
      speechSynthesis.cancel();
      
      const utterance = new SpeechSynthesisUtterance(text);
      
      // Set language with fallbacks for Malayalam
      if (language === 'ml') {
        utterance.lang = 'ml-IN';
        // Fallback to Hindi if Malayalam not available
        const voices = speechSynthesis.getVoices();
        const malayalamVoice = voices.find(voice => voice.lang.includes('ml'));
        const hindiVoice = voices.find(voice => voice.lang.includes('hi'));
        
        if (malayalamVoice) {
          utterance.voice = malayalamVoice;
        } else if (hindiVoice) {
          utterance.voice = hindiVoice;
          utterance.lang = 'hi-IN';
        }
      } else {
        utterance.lang = 'en-IN';
      }
      
      utterance.rate = 0.7;
      utterance.pitch = 1;
      utterance.volume = 1;
      
      utterance.onerror = (event) => {
        if (event.error !== 'interrupted') {
          console.error('Speech error:', event.error);
        }
      };
      
      speechSynthesis.speak(utterance);
    } catch (error) {
      console.error('Text-to-speech error:', error);
    }
  };

  useEffect(() => {
    const fetchData = () => {
      const API_URL = process.env.REACT_APP_API_URL;
      axios.get(`${API_URL}/api/spices/${selectedSpice}/ohlc`)
        .then(res => {
          const data = res.data.map(item => ({
            ...item,
            date: new Date(item.timestamp).toLocaleDateString()
          }));
          setPriceData(data);
          if (data.length > 0) {
            setCurrentPrice(data[data.length - 1]);
          }
        })
        .catch(console.error);
    };
    fetchData();
    const iv = setInterval(fetchData, 3600000);
    return () => clearInterval(iv);
  }, [selectedSpice]);

  const selectedSpiceInfo = spices.find(s => s.name === selectedSpice);
  
  const getPriceText = () => {
    if (!currentPrice || !selectedSpiceInfo) return language === 'ml' ? '  ' : 'Loading price';
    
    if (language === 'ml') {
      const priceInMalayalam = numberToMalayalam(Math.floor(currentPrice.close));
      return `${selectedSpiceInfo.ml}     ${priceInMalayalam}  `;
    } else {
      return `Current price of ${selectedSpiceInfo.label} is ${currentPrice.close} rupees per kilogram`;
    }
  };
  
  const priceText = getPriceText();

  return (
    <div className="min-h-screen bg-gradient-to-b from-yellow-50 to-orange-50 pb-20">
      {/* Header */}
      <header className="bg-white shadow-sm p-4">
        <div className="flex justify-between items-center max-w-md mx-auto">
          <h1 className="text-xl font-bold text-yellow-700">{language === 'ml' ? ' ' : 'Market Prices'}</h1>
          <button
            onClick={() => setLanguage(language === 'en' ? 'ml' : 'en')}
            className="px-3 py-1 bg-yellow-100 rounded-full text-sm font-medium"
          >
            {language === 'en' ? '' : 'English'}
          </button>
        </div>
      </header>

      {/* Spice Grid */}
      <div className="p-4">
        <div className="grid grid-cols-2 gap-4 mb-6">
          {spices.map(spice => (
            <button
              key={spice.name}
              onClick={() => setSelectedSpice(spice.name)}
              className={`p-6 rounded-2xl shadow-lg transform hover:scale-105 transition-all ${
                selectedSpice === spice.name
                  ? 'bg-yellow-500 text-white'
                  : 'bg-white text-gray-700'
              }`}
            >
              <img 
                src={spice.image} 
                alt={spice.label}
                className="w-12 h-12 object-cover rounded-lg mx-auto mb-2"
                onError={(e) => { e.target.src = '/images/placeholder.svg'; }}
              />
              <h3 className="font-bold text-sm">{spice.label}</h3>
              <p className="text-xs opacity-75">{spice.ml}</p>
            </button>
          ))}
        </div>

        {/* Current Price Card */}
        {currentPrice && (
          <div className="bg-white rounded-2xl shadow-lg p-6 mb-6">
            <div className="flex items-center justify-between mb-4">
              <div>
                <div className="flex items-center justify-center gap-3 mb-2">
                  <img 
                    src={selectedSpiceInfo?.image} 
                    alt={selectedSpiceInfo?.label}
                    className="w-8 h-8 object-cover rounded"
                    onError={(e) => { e.target.src = '/images/placeholder.svg'; }}
                  />
                  <h2 className="text-2xl font-bold text-gray-800">
                    {selectedSpiceInfo?.label}
                  </h2>
                </div>
                <p className="text-sm text-gray-500">
                  Updated: {new Date(currentPrice.timestamp).toLocaleDateString()}
                </p>
              </div>
              <button
                onClick={() => speak(priceText)}
                className="bg-yellow-500 text-white p-3 rounded-full shadow-lg hover:bg-yellow-600 transition-colors"
              >
                <span className="text-xl"></span>
              </button>
            </div>
            
            <div className="text-center">
              <div className="text-4xl font-bold text-green-600 mb-2">
                {currentPrice.close}/kg
              </div>
              <div className="flex justify-center space-x-4 text-sm">
                <span className="text-gray-600">High: {currentPrice.high}</span>
                <span className="text-gray-600">Low: {currentPrice.low}</span>
              </div>
            </div>
          </div>
        )}

        {/* Simple Price Chart */}
        {priceData.length > 0 && (
          <div className="bg-white rounded-2xl shadow-lg p-4">
            <h3 className="text-lg font-bold mb-4 text-center">30-Day Price Trend</h3>
            <ResponsiveContainer width="100%" height={200}>
              <LineChart data={priceData}>
                <XAxis 
                  dataKey="date" 
                  tick={{ fontSize: 10 }}
                  interval="preserveStartEnd"
                />
                <YAxis 
                  tick={{ fontSize: 10 }}
                  tickFormatter={(value) => `${value}`}
                />
                <Line 
                  type="monotone" 
                  dataKey="close" 
                  stroke="#10b981" 
                  strokeWidth={3}
                  dot={{ fill: '#10b981', strokeWidth: 2, r: 4 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
        )}
      </div>
    </div>
  );
}